

#root {
  width: 100%;
    height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.chatsBar .navbar {
  flex-wrap: nowrap;

}
.navbar{
  flex-wrap: inherit;
}

.search-input{
  width: 80%;
  border-radius: 5px;
}

.btn:hover {
  color: rgb(151, 151, 151);
  font-weight: bold;
      background: 0;

  background-color:0;
  border-color: rgb(185, 217, 225);
  border-width: 0;

}

.btn:focus,.btn:active {
   outline: none !important;
   box-shadow: none;
}



.content {
  flex: 1;
  padding: 20px;
}

.time{
  font-size: x-small;
  float: right;
}

.card-title {
  padding-inline-start: 40%;
  font-size: xx-large;
}



.card-body {
    flex: 0;
  padding: 0;
}


.btn{
    color: rgba(0, 0, 0, 0.896);
    font-weight: bold;
    background: 0;

    background-color: 0;
    border-color: rgb(185, 217, 225);
    border-width: 0px;
     --bs-btn-active-bg:  rgb(248, 245, 245);;
    --bs-btn-active-border-color: rgb(185, 217, 225);
}


#logout-btn{
   background-color: rgb(240, 88, 88);
   
}


#logout-btn:hover{
  color: black;
   background-color: rgb(243, 35, 35);
}

#add-btn{
   background-color: rgb(98, 151, 251);
}


#add-btn:hover{
  color: black;
   background-color: rgb(60, 85, 246);
}

#close-btn{
   background-color: rgb(143, 144, 146);
}


#close-btn:hover{
  color: black;
   background-color: rgb(134, 134, 135);
}


#toast-container {
  position: fixed;
  left: 20px;
  bottom: 20px;
  color: white;
  padding: 10px;
  border-radius: 10px;
  z-index: 100;
  
}
.toast-body {
    
   color: black;
}

.toast-header {
    size: 100%;
width: 100%;
}


.btn-group {
  float: right;
  padding-bottom: 10px;
  
}

.toast:not(.show) {
    display: flex;
}

.btn-text {
  font-size: large;
}
