.chatbox {
  width: 80%;
  background-color: rgb(224, 239, 253);
  display: flex;
  flex-direction: column; /* set flex direction to column */
  padding: 0;
  margin: 0;
  border-radius: 10px;
}

.side-bar {
  width: 100%; /* adjust width of side bar */
        scroll-behavior: smooth;
  overflow-y: scroll;
height: calc(80vh + 60px);

}

.text {
   height: 100%;
        max-height: 80vh;
       scroll-behavior: auto;
  overflow-y: scroll;
    
}



.content {
  flex: 1;
  padding: 20px;
}

/* width and color */
::-webkit-scrollbar {
  width: 10px;
  background-color:  rgb(224, 239, 253);
}

/* handle */
::-webkit-scrollbar-thumb {
  background-color: #98c3c8;
  border-radius: 20px;
}

/* handle on hover */
::-webkit-scrollbar-thumb:hover {
  background-color: #79c5de;
}

.list-group-item.active {
    z-index: 20;
    color: #fff;
    background-color: #75aaf7;
    border-color: #b9d3fa;
}

.search:disabled{
  visibility:hidden;
}

.list-group-item {
  padding: 20%;
  padding-left: 5%;
    padding-right: 10%;
  font-size: 14px;
}

.msg-bar {
 padding-left: 3px;
 display: flex;
 flex-wrap: initial;
}

.top-bar {
 
--bs-gutter-x: 0;
}

.chats{
      background-image: url('backgroundchat.webp');
  background-repeat: no-repeat;
  background-size: cover;
}

.row {
    --bs-gutter-x: 0;

}


.input-text {
  width: 98%;
  height: 60px;

}

.sendMsg{
flex-wrap: inherit;
}

#send-btn{
    background-color:rgb(224, 239, 253)
}

.contactName{
    width: 90%;
    font-weight: bold;
}



.chat-container {
  display: flex;
  flex-direction: column;
}

.chat-bubble {
  max-width: 70%;
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
  font-size: 16px;
  word-break: break-word;
}

#profie-img-showcase {
  height: 30vh;
}
.chat-bubble.sent {
  align-self: flex-start;
  background-color: #007bff;
  color: white;
}

#profile-tab{
  background-color: rgb(224, 239, 253);
}

.recived {
    max-width: 70%;
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
  font-size: 16px;
